import Vue from 'vue'
import Router from 'vue-router'
import routers from './routers'
import { IRouterMap } from '@/types/IRouterMap'

Vue.use(Router)

const routerMap: IRouterMap[] = [
  {
    path: '/',
    name: '',
    component: () => import('@/views/Index.vue'),
    meta: { title: '北京应用数学研究所' }
  },
  {
    path: '/404',
    component: () => import('@/views/Index.vue')
  },
  { path: '*', redirect: '/404' }
]

export default new Router({
  routes: routerMap.concat(routers)
})
