




























































































import { Vue, Component, Prop } from 'vue-property-decorator'
import { State, namespace } from 'vuex-class'
import { IRouterMap } from '@/types/IRouterMap'

const permission = namespace('permission')

@Component
export default class SideBar extends Vue {

  // @ts-ignore
  @permission.State routers


  get defaultActive(): string {
    const routePathArr = this.$route.path.split('/')
    if (routePathArr.length > 3) {
      return '/' + routePathArr[1] + '/' + routePathArr[2]
    } else {
      return this.$route.path
    }
  }

  get userRouters(): any {
    const routers: IRouterMap[] = this.routers

    return routers
  }
}
